import config from 'src/commons/config-hoc';
import {Button, Form} from 'antd';
import {useState} from 'react';
import {PageContent, FormItem, QueryBar} from '@ra-lib/components';

export default config({
    path: '/task',
})(function Task(props) {
    const [pageNum, setPageNum] = useState(1);
    const [pageSize, setPageSize] = useState(20);
    const [conditions, setConditions] = useState({name: 123});

    const params = {
        ...conditions,
        pageNum,
        pageSize,
    };

    // 获取角色列表
    const {
        data: {
            dataSource,
            total,
        } = {},
    } = props.ajax.usePost('/monitor/findTaskAll', params, [conditions, pageNum, pageSize], {
        // mountFire: false, // 初始化不查询
        formatResult: res => {
            return {
                dataSource: res?.data?.content || [],
                total: res?.data?.totalElements || 0,
            };
        },
    });

    const layout = {
        wrapperCol: {style: {width: 200}},
    };
    return (
        <PageContent>
            <QueryBar>
                <Form layout="inline">
                    <FormItem
                        {...layout}
                        label="创建日期"
                        type="date-range"
                        name="date"
                    />
                    <FormItem
                        {...layout}
                        label="创建时间"
                        type="time-range"
                        name="time"
                    />
                    <FormItem
                        {...layout}
                        label="创建日期时间"
                        type="date-time-range"
                        name="dateTime"
                    />
                </Form>
            </QueryBar>
            <Button onClick={() => setPageNum(1) || setPageSize(20) || setConditions({})}>查询</Button>

            <div>
                {dataSource?.length}
            </div>
            <div>
                {JSON.stringify(total)}
            </div>
        </PageContent>
    );

});
